@charset "utf-8";
@import "variables";
@import "mixins";
@import "reset";
@import "base";

[data-gsap="opacity"],
[data-gsap="groupOpacity"] {
    opacity: 0;
}

[data-gsap="scale"] {
    transform: scale(0.8);
    opacity: 0;
}

[data-gsap="translateY"],
[data-gsap="groupTranslateY"] {
    transform: translateY(50px);
}

[data-gsap="section-overlap"] {
}

[data-gsap="bg-reveal"] {
    position: relative;
    &.is-visible::after {
        left: 100%;
    }
}
[data-gsap="bg-reveal"]::after {
    content: "";
    position: absolute;
    background-color: #fff;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: 0.4s all ease-in-out;
}

.top-nav {
    background-color: $black;
    padding: 8px 0;
    &.fixed {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        z-index: 100;
        padding-top: 8px;
        padding-bottom: 8px;
        transform: translateY(-10px);
        opacity: 0;
        border-bottom: 1px solid #8d8e90;
        @include mq(768) {
            .top-menu__item {
                font-size: 16px;
            }
        }
        @include mq(768, max) {
            border-bottom: 0;
            background-color: transparent;
            .logo {
                opacity: 0;
            }
            .btn-menu-m {
                background-color: $black;
                border: 2px solid #fff;
                box-sizing: content-box;
            }
            .btn-menu-inner,
            .btn-menu-inner::before,
            .btn-menu-inner::after {
                background-color: #fff;
            }
        }
        .logo {
            width: 110px;
        }
        &.visible {
            transition: 0.2s all ease-in-out;
            opacity: 1;
            transform: translateY(0);
        }
    }
    @include mq(768) {
        padding: 20px 0;
    }
    .wrap {
        display: flex;
        align-items: center;
    }
    nav {
        margin-left: auto;
    }
    .logo {
        width: 110px;
        @include mq(1024) {
            width: 130px;
        }
        @include mq(1600) {
            width: 150px;
        }
        transform: skewY(0);
        transition: all 0.05s ease-in-out;
        img {
            border-radius: 0;
            width: 100%;
        }
    }
    a.logo:hover {
        transform: skewY(-5deg);
    }
}
.top-menu {
    display: flex;
    align-items: center;
    @include accent-font;
    @include mq(768) {
        &__item {
            margin-left: 40px;
        }
    }
    &__link {
        color: #fff;
        text-decoration: none;
        &:hover,
        &:focus {
            color: $primary-color;
        }
    }

    @include mq(768, max) {
        position: fixed;
        visibility: hidden;
        opacity: 0;
        height: 100vh;
        overflow-y: auto;
        left: 0;
        top: 0;
        z-index: 10;
        flex-direction: column;
        right: 0;
        justify-content: center;
        background-color: $black;
        transition: 0.3s all ease-in-out;
        transform: translateY(20px);

        &.is-visible {
            visibility: visible;
            opacity: 1;
            transform: translateY(0);
        }

        &__item {
            padding: 8px 0;
            order: 2;
        }
        &__item.lang-switcher {
            padding: 16px 0;
            order: 1;
        }
        &__link {
            padding: 8px;
            display: inline-block;
        }
    }
}
.lang-switcher {
    color: #fff;
    display: flex;
    align-items: baseline;
}
.lang-switcher__item {
    color: #fff;
    text-decoration: none;
    &.is-active::before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 4px;
        height: 4px;
        border-radius: 100%;
        background-color: $primary-color;
        box-shadow: 0px 0px 4px 2px #41cd44;
        margin-right: 10px;
    }
    &:not(.is-active):hover,
    &:not(.is-active):focus {
        color: $primary-color;
    }
}

.is-active .btn-menu-inner {
    -webkit-transition-delay: 0.12s;
    transition-delay: 0.12s;
    -webkit-transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: rotate(225deg);
    transform: rotate(225deg);
}

.is-active .btn-menu-inner::before {
    top: 0;
    -webkit-transition: top 0.1s ease-out, opacity 0.1s 0.12s ease-out;
    transition: top 0.1s ease-out, opacity 0.1s 0.12s ease-out;
    opacity: 0;
}

.is-active .btn-menu-inner::after {
    bottom: 0;
    -webkit-transition: bottom 0.1s ease-out, -webkit-transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: bottom 0.1s ease-out, -webkit-transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: bottom 0.1s ease-out, transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: bottom 0.1s ease-out, transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1),
        -webkit-transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: bottom 0.1s ease-out, transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1),
        -webkit-transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
}

.btn-menu-m {
    font: inherit;
    position: relative;
    z-index: 100;
    display: inline-block;
    margin: 0;
    cursor: pointer;
    transition-timing-function: linear;
    transition-duration: 0.15s;
    transition-property: opacity, filter;
    vertical-align: middle;
    text-transform: none;
    color: inherit;
    border: 0;
    background-color: #fff;
    width: 36px;
    height: 36px;
    border-radius: 100px;
    padding: 0;
    @include mq(768) {
        display: none;
    }
}

.btn-menu-box {
    position: relative;
    display: inline-block;
    width: 20px;
    vertical-align: middle;
}

.btn-menu-inner {
    top: 50%;
    display: block;
    margin-top: -2px;
    transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    transition-duration: 0.22s;
}

.btn-menu-inner,
.btn-menu-inner::before,
.btn-menu-inner::after {
    position: absolute;
    width: 20px;
    height: 2px;
    border-radius: 0;
    background-color: $black;
}

.btn-menu-inner::before,
.btn-menu-inner::after {
    display: block;
    content: "";
}

.btn-menu-inner::before {
    top: -6px;
    transition: top 0.1s 0.25s ease-in, opacity 0.1s ease-in;
}

.btn-menu-inner::after {
    bottom: -6px;
    transition: bottom 0.1s 0.25s ease-in, transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.homepage-intro {
    text-align: center;
    padding: 80px 0;
    @include mq(768) {
        min-height: calc(100vh - 250px);
    }
    display: flex;
    justify-content: center;
    margin-top: 0;
    flex-direction: column;
    align-items: center;
    @include mq(1600) {
        padding: 100px 0;
    }
    .greeting {
        font-size: 24px;
        @include mq(768) {
            font-size: 32px;
        }
        @include mq(1280) {
            font-size: 40px;
        }
        @include mq(1600) {
            font-size: 48px;
        }
        @include mq(1900) {
            font-size: 56px;
        }
    }
    h1 {
        font-size: 36px;
        line-height: 1;
        font-weight: 800;
        @include mq(768) {
            font-size: 72px;
            font-weight: 600;
        }
        @include mq(1024) {
            font-size: 80px;
        }
        @include mq(1280) {
            font-size: 96px;
        }
        @include mq(1366) {
            font-size: 108px;
        }
        @include mq(1600) {
            font-size: 148px;
        }
        .gradient-text {
            display: block;
            margin: 0.35em auto;
            @include mq(1024) {
                margin: 0.25em auto;
            }
        }

        .accent-font {
            display: block;
            margin: auto;
            color: #c5c5c5;
            font-size: 18px;
            @include mq(768) {
                font-size: 24px;
            }
            @include mq(1024) {
                font-size: 26px;
            }
            @include mq(1280) {
                font-size: 32px;
            }
            @include mq(1366) {
                font-size: 36px;
            }
            @include mq(1600) {
                font-size: 40px;
            }
        }
    }
    .btn {
        margin-top: 64px;
    }
    .intro-footnote {
        color: #999;
        margin-top: auto;
        margin-bottom: 0;
        font-size: 1em;
        padding-top: 40px;
    }

    .intro-footnote span {
        font-size: 30px;
        color: #edfb55;
        text-align: left;
        @include accent-font;
    }
}

.older-section {
    padding-top: 0;
}

.cases-section {
    overflow: hidden;
    position: relative;
    margin-top: 0;
    &::before {
        content: "";
        position: absolute;
        left: -2px;
        right: -2px;
        top: 0;
        border: 2px solid rgba(255, 255, 255, 0.9);
        border-bottom: 0;
        border-radius: $brs-l $brs-l 0 0;
        height: 60px;

        @include mq(1024) {
            border-radius: $brs-xxl $brs-xxl 0 0;
        }
    }
}

.caselist {
    display: grid;
    gap: 40px;
    align-items: stretch;
    @include mq(1280, max) {
        grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
    }
}

.caselist__item {
    border-radius: $brs-l;
    border: 1px solid rgba(255, 255, 255, 0.5);
    display: flex;
    padding: 40px;
    @include mq(1280) {
        padding: 52px;
    }
    position: relative;
    align-items: center;
    text-decoration: none;
    color: #fff;
    height: 100%;

    .item-content {
        display: flex;
        flex-direction: column;
        height: 100%;
        flex-grow: 1;
        &__bottom {
            padding-top: 24px;
            margin-top: auto !important;
        }
    }

    &.is-large {
        gap: 0 60px;
        .btn {
            align-self: flex-start;
        }
        @include mq(1280, max) {
            gap: 40px 0;
            flex-direction: column-reverse;
            justify-content: flex-end;
            align-items: stretch;
            .item-title {
                max-width: none;
            }
            .item-date {
                margin-top: auto !important;
                padding: 24px 0;
            }
            .btn {
                align-self: stretch;
                margin-top: auto;
            }
        }
        @include mq(576, max) {
            /*             padding: 0;
            border: 0 !important;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1)!important;
            box-shadow: none !important;
            background-color: transparent !important;
            margin-bottom: 60px;
            gap: 20px; */
            padding: 20px;
            border-radius: 16px;
            margin-bottom: 24px;
            gap: 24px;
            .item-title {
                font-size: 32px;
                line-height: 1.2;
            }
        }
        .item-image {
            width: 100%;
            @include mq(1280) {
                width: 550px;
            }
            @include mq(1366) {
                width: 600px;
            }
            @include mq(1600) {
                width: 650px;
            }
            flex-shrink: 0;
            margin-left: auto;
        }
        img {
            border-radius: 0;
        }
    }
    &--last {
        border: 0;
        background-color: #303135 !important;
        box-shadow: none !important;
    }
    &:hover {
        background-color: rgba(255, 255, 255, 0.1);
        border-color: $primary-color;
        box-shadow: 0 0 0 2px $primary-color;
        .item-title {
            color: $primary-color;
        }
    }
    .item-title {
        font-weight: 500;
        font-size: 36px;
        transition: 0.2s all ease-in-out;
        &:hover {
            color: $primary-color;
        }
        @include mq(1280) {
            font-size: 40px;
        }
        @include mq(1366) {
            font-size: 44px;
        }
        line-height: 1.3;
        text-decoration: none;
        color: #fff;
        max-width: 12ch;
        display: inline-block;
    }
    .taglist__item {
        color: #c9c9c9;
        font-size: 0.65em;
    }

    .item-content > * + * {
        margin-top: 30px;
        @include mq(1024, max) {
            margin-top: 24px;
        }
        @include mq(768, max) {
            margin-top: 16px;
        }
    }

    .item-date {
        @include accent-font;
        font-size: 15px;
        color: rgba(255, 255, 255, 0.9);
        position: relative;
        display: flex;
        align-items: center;
        &::before,
        &::after {
            content: "";
            display: inline-block;
            border: 1px solid rgba(255, 255, 255, 0.5);
        }
        &::before {
            width: 32px;
        }
        &::after {
            flex-grow: 1;
        }
        span {
            display: inline-block;
            padding: 0 10px;
        }
    }
}

.caselist__item.is-vertical {
    margin-bottom: 0;
    @include mq(576, max) {
        border-radius: 16px;
        padding: 20px;
    }
    @include mq(1280) {
        padding: 36px;
    }
    @include mq(1920) {
        padding: 48px;
    }
    .item-title {
        @include mq(1600, max) {
            font-size: 36px;
        }
        @include mq(1024, max) {
            font-size: 32px;
        }
    }
    .btn {
        display: block;
    }
    .item-content > * + * {
        margin-top: 16px;
        @include mq(1366) {
            margin-top: 24px;
        }
    }
    .taglist__item {
        font-size: 0.65em;
    }
    .stats-block {
        margin-top: 12px;
    }
    .item-desc {
        font-size: 19px;
    }
}

.caselist--small {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 32px;
}

.taglist {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
    @include mq(1280) {
        gap: 16px;
    }
    &__item {
        @include accent-font;
        line-height: 1;
        padding: 0.5em 0.7em;
        border-radius: $brs-xxs;
        border: 1px solid $primary-color;
        text-transform: lowercase;
    }
    &--small {
        font-size: 12px;
        @include mq(768) {
            font-size: 16px;
        }
    }
}

.stats-block {
    @include accent-font;
    display: flex;
    font-weight: 700;
    align-items: center;
    font-size: 32px;
    line-height: 1;
    .stats-number {
        font-size: 1.5em;
        font-weight: 900;
        margin-right: 8px;
    }
    .stats-unit {
        align-self: baseline;
        font-size: 0.7em;
    }
}

.stats-desc {
    font-size: 17px;
    line-height: 1.2;
    opacity: 0.7;
    margin-top: 12px;
}

