.timeline {
    max-width: 940px;
    margin-left: auto;
    margin-right: auto;
}

.timeline-item {
    margin-bottom: 10px;
    .timeline-item__date {
        padding: 0;
    }
    .timeline-item__info {
        flex-basis: 0;
        text-align: center;
        &::after {
            margin-top: 10px;
        }
    }
    &__info {
        font-size: 0.9em;
    }
    &__content {
        flex-grow: 1;
        @include gradient-border;
        padding: 24px;

        @include mq(768) {
            padding: 32px;
        }
        @include mq(1280) {
            padding: 48px;
        }
        @include mq(1600) {
            padding: 64px;
        }
    }
    &__date {
        font-weight: 600;
        padding-top: 68px;
    }
    &__place {
        color: #666;
    }
}

.timeline-item__info::after,
.timeline-item::after {
    content: "";
    display: block;
    height: 50px;
    width: 4px;
    background-color: #50d7c6;
    margin: 0 auto;
}
.timeline:last-of-type {
    .timeline-item:last-child::after {
        display: none;
    }
}

[data-btn="show-jobs"] {
    display: block;
    margin: auto;
}

footer {
