    text-align: center;
    padding: 12px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    a {
        color: #ebebeb;
        text-decoration: underline;
        &:hover {
            color: $primary-color;
        }
    }
}

.experience-section-outer {
    background-color: $black;
