        border-radius: $brs-xxl;
    }
}

.skills-grid {
    display: grid;
    grid-template-columns: none;
    @include mq(1024) {
        grid-template-columns: 1fr 1fr;
    }
    gap: 60px 48px;
}
.skills-callout {
    justify-self: start;
    @include accent-font;
    font-size: 20px;
    font-weight: 700;
    .callout {
        margin-top: 0;
    }
    @include mq(576) {
