    }
}

.about-group {
    display: flex;
    flex: 1 0 auto;
    gap: 100px;
    @include mq(768, max) {
        flex-direction: column;
    }
    .about-pic {
        width: 30%;
        flex-shrink: 0;
    }
}

.contacts-block {
    padding-top: 32px;
    margin-top: 32px;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.title-group + * {
    margin-top: 32px;
}

