        }
    }
    h3 {
        @include mq(1280, max) {
            font-size: 24px;
        }
        @include mq(1024, max) {
            font-size: 20px;
        }
        @include mq(768, max) {
            font-size: 16px;
            margin: 24px 0;
        }
        font-weight: 300;
        margin: 40px 0;
    }
    .taglist {
        display: inline-flex;
        font-size: 12px;
        @include mq(768) {
            font-size: 14px;
        }
        @include mq(1280) {
            font-size: 16px;
        }
    }
    &__date {
        @include accent-font;
        font-size: 15px;
        color: rgba(255, 255, 255, 0.9);
        position: relative;
        display: flex;
        align-items: center;
        margin: 40px auto 0;
        max-width: 360px;
        &::before,
        &::after {
            content: "";
            display: inline-block;
            border: 1px solid rgba(255, 255, 255, 0.5);
            flex-grow: 1;
        }
        span {
            display: inline-block;
            padding: 0 10px;
        }

        @include mq(768, max) {
            max-width: none;
            margin: 24px 0 0;
            &::before {
                flex-grow: 0;
                width: 40px;
            }
        }
    }
}

video {
    max-width: 100%;
    position: relative;
    display: block;
}
video,
.video__play-button {
    border-radius: $brs-xxs;
    @include mq(768) {
        border-radius: $brs-m;
    }
}

.dark-bg video {
    // border: 1px solid #fff;
}

.screens-grid-2 {
    display: grid;
    // grid-template-columns: 1fr 1fr;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 32px;
    @include mq(1024) {
        gap: 60px;
    }
