}
.mobile-screens {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 32px;
    max-width: 940px;
    margin: 40px auto;
    @include mq(768) {
        gap: 60px;
        margin: 80px auto;
        .grid-item:nth-child(2) {
            padding-top: 100px;
        }
        .grid-item:nth-child(3) {
            padding-top: 30px;
        }
    }

    @include mq(1280) {
        gap: 100px;
        max-width: 1100px;
    }

    img {
        border-radius: $brs-xs;
        @include mq(768) {
            border-radius: $brs-s;
        }
    }
}
@include mq(576) {
    [class*="media-bg-layer"] .mobile-screens {
        gap: 40px;
        margin: 20px;
    }
}

@include mq(576, max) {
    .mobile-screens {
        overflow-x: auto;
        gap: 5vw;
        -webkit-overflow-scrolling: touch;
        margin: 36px -16px;
        /*         ::-webkit-scrollbar {
            display: none;
        }
        scrollbar-width: none; */
        picture,
        img {
            width: 60vw;
            max-width: none;
        }

        .grid-item:first-child {
            padding-left: 16px;
        }

        .grid-item:last-child {
            padding-right: 16px;
        }
    }
}

.sided-callout-layout {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    @include mq(1024) {
        gap: 60px;
    }
    align-items: flex-start;
    .text-block {
        flex-basis: 0;
        flex-grow: 999;
        min-inline-size: 40%;
    }
    aside {
        flex-grow: 1;
        flex-basis: 360px;
        .callout {
            margin-top: 0;
        }
    }
}

.pages-type-layout {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    @include mq(1024) {
        gap: 60px;
    }
    .numbered-list {
        flex-basis: 300px;
        flex-grow: 1;
    }
    .block-image {
        flex-basis: 0;
        flex-grow: 999;
        min-inline-size: 50%;
        max-inline-size: 750px;
    }
}

.solution-layout {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    @include mq(1024) {
        gap: 60px;
    }
    .solution-desc {
        flex-basis: 320px;
        flex-grow: 1;
    }
    .solution-image {
        flex-basis: 0;
        flex: 999;
        min-inline-size: 55%;

        > * + * {
            margin-top: 48px;
        }
    }
    .solution-desc__inner {
        position: sticky;
        top: 72px;
    }
    + .solution-layout {
        margin-top: 32px;
        @include mq(768) {
            margin-top: 72px;
        }
    }
}
.underline-red,
.underline-green {
    text-decoration: underline;
    text-underline-offset: -4px;
    text-decoration-skip-ink: none;
    text-decoration-thickness: 8px;
}
.underline-red {
    text-decoration-color: #fe6d6d;
}
.underline-green {
    text-decoration-color: #11e99b;
}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
    gap: 32px;
    margin-bottom: 32px;
    &__item {
        @include gradient-border;
        &::before {
            background-color: $black;
        }
        .stats-block {
            align-items: baseline;
        }
    }
}

.links-section {
    text-align: center;
    .buttons-group {
        justify-content: center;
    }
}

[class*="media-bg-layer"] {
    padding: 16px;
    margin-left: -16px;
    margin-right: -16px;
    @include mq(768) {
        margin: 0;
        border-radius: 36px;
        padding: 20px;
    }

    > * + * {
        margin-top: 32px;
    }
}

.media-bg-layer--dark {
    background-color: $black;
}
.media-bg-layer--dark-green {
    background-color: #284356;
    .btn-zoom {
        border-color: #284356;
    }
}
.media-bg-layer--dark-gray {
    background-color: #3b3f4b;
    .btn-zoom {
        border-color: #3b3f4b;
    }
}
.media-bg-layer--medium-gray {
    background-color: #c3ced4;
    .btn-zoom {
        border-color: #c3ced4;
    }
}
.media-bg-layer--gray {
    background-color: #e9e9e9;
    .btn-zoom {
        border-color: #e9e9e9;
    }
}

@include mq(768) {
    [class*="columns-list"] {
        column-gap: 30px;
        max-width: 660px;
        columns: 2;
        li {
            page-break-inside: avoid;
            break-inside: avoid-column;
            &:first-child {
                margin-top: 0;
            }
        }
    }
    .columns-list--3 {
        @include mq(1024) {
            max-width: 1200px;
            columns: 3;
        }
    }
}
